<template>
    <div class="p-20">

        <div class="filter-container" style="margin-bottom: 20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入关键词"
                      @keyup.native.enter="initData"
                      v-model="categoryQuery.queryStr">
            </el-input>
            <el-button class="filter-item" @click="initData" size="small">查询</el-button>
            <el-button type="primary" @click="showAddDialog" v-if="permission.add" size="small">新增</el-button>
        </div>

        <el-table
                ref="singleTable"
                :data="tableData"
                style="width: 100%">
            <el-table-column type="index" label="序号" width="80"></el-table-column>
            <el-table-column
                    v-for="item in tableHeader"
                    :key="item.prop"
                    :label="item.label"
                    :prop="item.prop"
                    :width="item.width"
                    :min-width="item.minWidth"
                    :align="item.align"
                    :formatter="item.formatter"
                    show-overflow-tooltip>
            </el-table-column>
            <el-table-column
                    fixed="right"
                    label="操作"
                    width="150">
                <template scope="scope" class="filter-operation">
                    <ul style="list-style: none;padding: 0px">
                        <li style="float:left;width: 30px">
                            <el-button
                                    type="text" size="small"
                                    @click="getCategory(scope.row.id)" v-if="permission.edit">修改
                            </el-button>
                        </li>
                        <li style="float:left;width: 30px">
                            <el-button
                                    type="text" size="small"
                                    @click="showDeleteDialog(scope.row.id)" v-if="permission.delete">删除
                            </el-button>
                        </li>
                        <li style="float:left;width: 30px" v-if="scope.row.status===1">
                            <el-button
                                    type="text" size="small"
                                    @click="enableDisable(2,scope.row.id,'启用')" v-if="permission.enable">启用
                            </el-button>
                        </li>
                        <li style="float:left;width: 30px" v-else-if="scope.row.status===2">
                            <el-button
                                    type="text" size="small"
                                    @click="enableDisable(1,scope.row.id,  '禁用')" v-if="permission.disable">禁用
                            </el-button>
                        </li>
                    </ul>
                </template>
            </el-table-column>
        </el-table>

        <div class="pagination">
            <el-pagination @size-change="userPageSize" @current-change="userPage"
                           :current-page="categoryQuery.pageNum"
                           :page-sizes="[10,20,30, 50]" :page-size="categoryQuery.pageSize"
                           layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>

        <el-dialog title="自动交易信息" :visible.sync="dialogVisible" @close="dialogClose" :close-on-click-modal="false">
            <el-form :model="data" :rules="rules" ref="data">
                <el-form-item :label-width="itemWidth" label="*交易类型" prop="tradeMappingId">
                    <el-select v-model="data.tradeMappingId" placeholder="请选择">
                        <el-option
                                v-for="item in mappingList"
                                :key="item.id"
                                :label="item.baseName + '-'+ item.tradeName"
                                :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*类型" prop="tradeType">
                    <el-select v-model="data.tradeType" placeholder="请选择">
                        <el-option
                                v-for="item in tradeTypeOptions"
                                :key="item.key"
                                :label="item.value"
                                :value="item.key">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*用户UID" prop="userId">
                    <el-input-number class="input-short" v-model="data.userId" :min="1"
                                     :max="999999999"
                                     auto-complete="off"></el-input-number>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*最小交易数量" prop="minQuantity">
                    <el-input class="input-short" v-model="data.minQuantity"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*最大交易数量" prop="maxQuantity">
                    <el-input class="input-short" v-model="data.maxQuantity"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*取价类型" prop="priceType">
                    <el-select v-model="data.priceType" placeholder="请选择">
                        <el-option
                                v-for="item in priceTypeOptions"
                                :key="item.key"
                                :label="item.value"
                                :value="item.key">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*最小浮动价格" prop="minPrice">
                    <el-input class="input-short" v-model="data.minPrice"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*最大浮动价格" prop="maxPrice">
                    <el-input class="input-short" v-model="data.maxPrice"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*多少分钟一次" prop="intervalMinute">
                    <el-input-number class="input-short" v-model="data.intervalMinute" :min="1"
                                     :max="999999999"
                                     auto-complete="off"></el-input-number>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*开始前随机停几秒" prop="suspendSecond">
                    <el-input-number class="input-short" v-model="data.suspendSecond" :min="1"
                                     :max="999999999"
                                     auto-complete="off"></el-input-number>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="validate">保 存</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>

    import fetch from '@/utils/fetch';
    import {validateEightDecimal} from '@/utils/validate';
    import DCTP from '@/utils/dctp';

    import calc from "@/utils/calculate-big-num";

    const tradeTypeOptions = [
        {key: 0, value: '买+卖'}
    ];

    const priceTypeOptions = [
        {key: 0, value: '平台自定义'}
    ];

    export default {
        data() {
            let validate = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('不能为空'));
                }
                callback();
            };
            let validateDecimal = (rule, value, callback) => {
                if (value === '' || !validateEightDecimal(value) || value * 1 > 999999999 || value * 1 < 0.00000001) {
                    callback(new Error('请输入正确的数值'));
                }
                callback();
            };
            return {
                itemWidth: '160px',
                dialogVisible: false,
                data: {
                    id: '',
                    tradeMappingId: '',
                    tradeType: '',
                    userId: '',
                    minQuantity: '',
                    maxQuantity: '',
                    priceType: '',
                    minPrice: '',
                    maxPrice: '',
                    intervalMinute: '',
                    suspendSecond: ''
                },
                action: '',
                rules: {
                    tradeMappingId: [{validator: validate, trigger: 'blur'}],
                    tradeType: [{validator: validate, trigger: 'blur'}],
                    userId: [{validator: validate, trigger: 'blur'}],
                    minQuantity: [{validator: validateDecimal, trigger: 'blur'}],
                    maxQuantity: [{validator: validateDecimal, trigger: 'blur'}],
                    priceType: [{validator: validate, trigger: 'blur'}],
                    minPrice: [{validator: validateDecimal, trigger: 'blur'}],
                    maxPrice: [{validator: validateDecimal, trigger: 'blur'}],
                    intervalMinute: [{validator: validateDecimal, trigger: 'blur'}],
                    suspendSecond: [{validator: validateDecimal, trigger: 'blur'}]
                },
                categoryQuery: {
                    queryStr: '',
                    pageNum: 1,
                    pageSize: 10
                },
                total: 0,
                tableData: [],
                tableHeader: [{
                    label: "UID",
                    prop: "userId",
                    width: 65
                }, {
                    label: "类型",
                    prop: "tradeType",
                    width: 100,
                    formatter(row, column, value) {
                        return value === 0 ? '买+卖' : '未知';
                    }
                }, {
                    label: "价格同步类型",
                    prop: "priceType",
                    width: 120,
                    formatter(row, column, value) {
                        return value === 0 ? '平台自定义' : '未知';
                    }
                }, {
                    label: "状态",
                    prop: "status",
                    width: 80,
                    align: 'center',
                    formatter(row, column, value) {
                        return value === 1 ? '未生效' : (value === 2 ? '生效' : '未知');
                    }
                }, {
                    label: "交易类型",
                    prop: "tradeName",
                    width: 130
                }, {
                    label: "最小交易数量",
                    prop: "minQuantity",
                    width: 180,
                    formatter(row, column, value) {
                        return calc.multiply( value , 1 , 12 , 2 , 1 ).toString()
                    }
                }, {
                    label: "最大交易数量",
                    prop: "maxQuantity",
                    minWidth: 120,
                    formatter(row, column, value) {
                        return calc.multiply( value , 1 , 12 , 2 , 1 ).toString()
                    }
                }, {
                    label: "最小浮动价",
                    prop: "minPrice",
                    minWidth: 120,
                    formatter(row, column, value) {
                        return calc.multiply( value , 1 , 12 , 2 , 1 ).toString()
                    }
                }, {
                    label: "最大浮动价",
                    prop: "maxPrice",
                    width: 120,
                    formatter(row, column, value) {
                        return calc.multiply( value , 1 , 12 , 2 , 1 ).toString()
                    }
                }, {
                    label: "多少分钟一次",
                    prop: "intervalMinute",
                    width: 120
                }, {
                    label: "开始前随机停几秒",
                    prop: "suspendSecond",
                    width: 170
                }, {
                    label: "创建时间",
                    prop: "createTime",
                    width: 170,
                    formatter(row, column, value) {
                        return DCTP.utils.parseDateTimeStr(value)
                    }
                }],
                mappingList: [],
                tradeTypeOptions,
                priceTypeOptions
            }
        },
        created() {
            this.initData();
            this.getMapping();
        },
        methods: {
            addCategory() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/trademapauto',
                        method: 'post',
                        data: _this.data
                    }).then(function (response) {
                        if (response.result) {
                            _this.dialogVisible = false;
                            _this.initData();
                        }
                        _this.getMessage(response.msg, response.result);
                    }).catch();
                })
            },
            showAddDialog() {
                this.dialogVisible = true;
                this.action = 'add';
            },
            validate() {
                this.$refs['data'].validate((valid) => {
                    if (valid) {
                        switch (this.action) {
                            case 'add':
                                this.addCategory();
                                break;
                            default:
                                this.modify();
                                break;
                        }
                    }
                });
            },
            dialogClose() {
                this.resetData();
            },
            setData(data) {
                let key;
                for (key in this.data) {
                    this.data[key] = data[key] === null ? '' : data[key];
                }
            },
            resetData() {
                let key;
                this.$refs['data'].resetFields();
                for (key in this.data) {
                    switch (key) {
                        default:
                            this.data[key] = '';
                    }
                }
                this.action = '';
            },
            getList() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: '/admin/trademapauto',
                        method: 'get',
                        params: _this.categoryQuery
                    }).then(function (response) {
                        if (response.result) {
                            _this.tableData = response.data.data;
                            _this.total = response.data.page.count;
                        }
                    }).catch();
                })
            },
            getCategory(id) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/trademapauto/' + id,
                        method: 'get'
                    }).then(function (response) {
                        if (response.result) {
                            _this.setData(response.data);
                            _this.dialogVisible = true;
                        }
                    }).catch();
                })
            },
            deleteCategory(id) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/trademapauto/' + id,
                        method: 'delete'
                    }).then(function (response) {
                        if (response.result) {
                            _this.initData();
                        }
                        _this.getMessage(response.msg, response.result);
                    }).catch();
                })
            },
            showDeleteDialog(id) {
                this.$confirm('确认删除？', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteCategory(id);
                }).catch(() => {
                    this.$message.info('操作取消');
                });
            },
            modify() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/trademapauto',
                        method: 'put',
                        data: _this.data
                    }).then(function (response) {
                        if (response.result) {
                            _this.dialogVisible = false;
                            _this.initData();
                        }
                        _this.getMessage(response.msg, response.result);
                    }).catch();
                })
            },
            query() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/trademapauto/query',
                        method: 'get',
                        params: _this.categoryQuery
                    }).then(function (response) {
                        if (response.result) {
                            _this.tableData = response.data.data;
                            _this.total = response.data.page.count;
                        }
                    }).catch();
                })
            },
            initData() {
                if (this.categoryQuery.queryStr === '') {
                    this.getList();
                } else {
                    this.query();
                }
            },
            //翻页的按钮
            userPage(val) {
                this.categoryQuery.pageNum = val;
                this.initData();
            },
            //获取每页多少条
            userPageSize(val) {
                this.categoryQuery.pageSize = val;
                this.initData();
            },
            getMapping() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: '/admin/trademapping/1/query',
                        method: 'post',
                        data: {
                            page: {
                                pageNum: 1,
                                pageSize: 100
                            }
                        }
                    }).then(function (response) {
                        if (response.result) {
                            _this.mappingList = response.data.data;
                        }
                    }).catch();
                })
            },
            //启用禁用
            enableDisable(int, id, str) {
                this.$confirm('要' + str + '此自动交易,是否继续', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let url = 'admin/trademapauto/';
                    if (int === 1) {
                        this.enableDisableMatch(url + 'forbid/' + id);
                    } else if (int === 2) {
                        this.enableDisableMatch(url + 'enable/' + id);
                    }
                }).catch(() => {
                    this.$message.info('操作已取消');
                });
            },
            enableDisableMatch(url) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url,
                        method: 'put'
                    }).then(function (response) {
                        _this.getMessage(response.msg, response.result);
                        _this.initData();
                    }).catch();
                });
            }
        }
    }
</script>



